import React, { useState } from 'react';
import { useDispatch } from 'react-redux';
import { commentSave } from '../../_actions/comment_action';
import useInput from '../../hooks/useInput';
import { withRouter } from 'react-router';
import { Button, Input } from 'antd';
function CommentEdit({ history, match }) {
  const { TextArea } = Input;

  const dispatch = useDispatch();
  const [content, onChange, setContent] = useInput('');
  const onSubmit = (e) => {
    e.preventDefault();
    let body = {
      postId: +match.params.id,
      content: content,
    };
    dispatch(commentSave(body)) //error 옮겨야함
      .then((response) => {
        window.location.reload();
      })
      .catch((error) => {
        switch (error.response?.status) {
          case 401:
            alert('로그인이 필요합니다.');
            history.push('/');
            break;
          case 403:
            alert('접근 권한이 없습니다');
            break;
          default:
            break;
        }
      });

    setContent('');
  };
  return (
    <div style={{ width: '900px' }}>
      <TextArea
        style={{ display: 'inline-block', width: '750px' }}
        size={'large'}
        rows={4}
        autoSize={{ minRows: 4, maxRows: 4 }}
        showCount
        maxLength={100}
        type="text"
        placeholder="댓글을 입력하세요"
        value={content}
        onChange={onChange}
      />
      <Button
        style={{ width: '120px', height: '113px', position: 'absolute' }}
        onClick={onSubmit}
      >
        {' '}
        댓글 입력{' '}
      </Button>
    </div>
  );
}

export default withRouter(CommentEdit);
